/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 15rem;
  background-image: url(../img/1.jpg);
  background-size: cover;
  background-position: center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .app {
  width: 100%;
  margin: 0 auto;
  padding: 3rem 0;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: scroll;
    overflow-x: hidden;
    margin-right: 0;
    padding-right: 1.5rem;
    padding-left: 1.5rem;
  }
  .contenedor-app .app::-webkit-scrollbar {
    width: 0.9rem;
  }
  .contenedor-app .app::-webkit-scrollbar-thumb {
    background-color: rgba(255, 255, 255, 0.4);
    border-radius: 5px;
  }
}

.barra {
  margin: 5rem;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra .boton {
  height: 5rem;
  transition-property: transform;
  transition-duration: 300ms;
  border-radius: 1rem;
  background-color: #329f00;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
  margin: 0;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: #226c00;
  cursor: pointer;
}
.barra .boton:hover {
  transform: scale(1.1);
}

/* Estilos por defecto */
#btnServicios,
#btnProductos,
#btnUsuarios,
#btnMovimientos {
  background-color: #FFFFFF;
  color: #329f00;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

/* Estilos cuando se presiona */
#btnServicios.presionado,
#btnProductos.presionado,
#btnUsuarios.presionado,
#btnMovimientos.presionado,
.despliegue {
  background-color: #226c00;
  color: #FFFFFF;
}

/* Estilos cuando se hace hover */
#btnServicios:hover,
#btnProductos:hover {
  cursor: pointer;
  background-color: #226c00;
  color: #FFFFFF;
}

.mostrar-botones {
  width: 95%;
  margin: 0 1.5rem;
}

.imagen2 {
  background-image: url(../img/2.gif);
  background-size: contain;
  background-repeat: no-repeat; /* Asegura que la imagen no se repita */
  background-position: center;
  height: 100vh; /* Por defecto */
}
@media (min-width: 768px) {
  .imagen2 {
    height: 140vh; /* Cambio en dispositivos tablet y más pequeños */
  }
}

.barra-servicios {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barra-servicios {
    display: flex;
    justify-content: space-between;
  }
}
.barra-servicios .boton {
  margin: 0 0 1rem 0;
}

.mostrar-botones {
  position: relative; /* Establecer el contenedor como posición relativa */
  width: 95%;
  margin: 0 1.5rem;
}

.opciones-servicios {
  position: absolute; /* Cambiar a posición absoluta */
  top: calc(100% + 5px); /* Posicionar debajo del botón "Servicios" */
  left: 0; /* Alinear con el borde izquierdo del contenedor */
  padding: 0; /* Ajustar según sea necesario */
  z-index: 100; /* Asegurar que esté por encima de otros elementos */
  width: 100%; /* Ajustar el ancho */
  display: flex; /* Alinear los botones en columna */
  flex-direction: column; /* Alinear los botones en columna */
}

/* Estilos para los enlaces con la clase 'boton-servicios' */
.boton-servicios {
  background-color: #FFFFFF;
  color: #329f00;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
  width: 100%; /* Ancho máximo para los botones */
  text-align: left; /* Alinear texto a la izquierda */
  margin-bottom: 5px; /* Espacio entre botones */
}

/* Estilos cuando se hace hover */
.boton-servicios:hover {
  color: #FFFFFF;
  background-color: #226c00;
  cursor: pointer;
}

.opciones-servicios .boton-servicios {
  font-weight: bold; /* Hacer que el texto sea negrita */
}

.imagen3 {
  background-image: url(../img/2.gif);
  background-size: contain;
  background-repeat: no-repeat; /* Asegura que la imagen no se repita */
  background-position: center;
  height: 100vh; /* Por defecto */
}
@media (min-width: 768px) {
  .imagen3 {
    height: 140vh; /* Cambio en dispositivos tablet y más pequeños */
  }
}

.seccion {
  display: none;
  padding: 5rem 0 0 0;
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-productos {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listado-productos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-productos .producto {
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 300ms;
  text-align: center;
}
.listado-productos .producto.seleccionado {
  background-color: #329f00;
}
.listado-productos .producto.seleccionado p {
  color: #FFFFFF;
}
.listado-productos .producto:hover {
  transform: scale(1.1);
}
.listado-productos .producto p {
  color: #1a1b15;
  margin: 0 0 1.25rem 0;
}
.listado-productos .producto .precio-producto {
  color: #329f00;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}

.contenido-resumen p {
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #329f00;
  font-weight: 700;
  font-size: 2.4rem;
}

.contenedor-producto {
  padding: 2rem 0;
  border-bottom: 1px solid #329f00;
}
.contenedor-producto:last-of-type {
  margin-bottom: 5rem;
}

.pedidos, .productos {
  list-style: none;
  padding: 0;
}
.pedidos li, .productos li {
  padding: 1rem;
  border-top: 1px solid #FFFFFF;
}
.pedidos li p, .productos li p {
  font-weight: 700;
  color: #329f00;
}
.pedidos li span, .productos li span,
.pedidos li .servicio,
.productos li .servicio {
  font-weight: 400;
  color: #FFFFFF;
}

.productos .acciones {
  margin: 0;
}
.productos .boton,
.productos .boton-eliminar {
  margin: 0;
}

.oculto {
  display: none;
}

.opciones-servicios:not(.oculto) {
  display: flex;
}

.subh2 {
  margin: 4rem 0;
}

.subh3 {
  margin: 0 0 4rem 0;
}

.formulario {
  margin-top: 5rem;
  width: 100%;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label {
  flex: 0 0 10rem;
}
.campo input {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.campo input:disabled {
  background-color: white;
  color: #1a1b15;
  cursor: not-allowed;
}

.input-password {
  width: 100%;
  position: relative;
}

.toggle-password {
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}

#password {
  width: 100%;
}

.campo1 {
  width: 95%;
  padding: 0 5rem 0 0;
  display: flex;
  align-items: center;
}
.campo1 label {
  flex: 0 0 10rem;
}
.campo1 input {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.campo1 input:disabled {
  background-color: white;
  color: #1a1b15;
  cursor: not-allowed;
}

.boton {
  background-color: #329f00;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
}
.boton:hover {
  background-color: #226c00;
  cursor: pointer;
}

.boton-eliminar {
  background-color: #cb0000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .boton-eliminar {
    width: auto;
  }
}
.boton-eliminar:hover {
  background-color: #980000;
  cursor: pointer;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.acciones a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2.5rem;
  text-align: center;
}

.alerta {
  padding: 1.25rem;
  color: #FFFFFF;
  width: 100%;
  display: block;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #cb0000;
}
.alerta.exito {
  background-color: #329f00;
}

@media (min-width: 768px) {
  .tabs,
  .mostrar-botones {
    display: flex;
  }
}
.tabs button,
.mostrar-botones button {
  background-color: #329f00;
  border: none;
  color: #FFFFFF;
  padding: 1rem;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: background-color 300ms ease;
}
.tabs button:hover,
.mostrar-botones button:hover {
  cursor: pointer;
  background-color: #226c00;
}
.tabs button.actual,
.mostrar-botones button.actual {
  background-color: #226c00;
  color: #FFFFFF;
}

.oculto2 {
  display: none;
}

.paginacion {
  padding: 2rem 0;
}
@media (min-width: 768px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
  }
}
.paginacion button {
  margin: 0 0 1rem;
}

.ocultar {
  visibility: hidden;
}